
.Loading{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  padding-top: 80px;
}

.ModalBtnGroup{
  display: -ms-flexbox;
  display: flex;
  margin-top: 15px;
}

.MenuLink{
  width: 100%;
  display: -ms-flexbox;
  display: flex;
}


.MenuBox{
  display: -ms-flexbox;
  display: flex;
  position: static;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin-top: 20px;
  /* min-width: 250px; */

}
.MenuBox a{
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

@media(min-width: 768px){
  .MenuBox{
    -ms-flex-direction: row;
        flex-direction: row;
    margin-top: 50px;
    position: absolute;
    left: 10px;
    bottom: 20px;
  }
  .MenuBox a{
    width: auto;
  }
}

.SectionTitle{
  margin: 80px 10px 30px;
  /* background-color: #f9f9f9; */
  border-bottom: solid 3px #aaa;
  position: relative;

  text-align: center;
  font-size: 26px;
  font-weight: 600;
  padding: 12px;
}

@media(min-width: 768px){
  .SectionTitle{
    max-width: 300px;
    margin: 80px auto 30px;
  }
}

.SectionTitle:after {
  position: absolute;
  content: " ";
  display: block;
  border-bottom: solid 3px grey;
  bottom: -3px;
  left: 0;
  width: 20%;
}


.Note{
  margin: 0 10px;
  text-align: center;
}

@media(min-width: 768px){
  .Note{
    max-width: 300px;
    margin: 0 auto;
  }
}



